export type DividerActionConfig = { type: 'divider' };

export type BaseActionItemConfig = {
  id: string;
  label?: string;
  skin?: string;
};

export interface Feedback {
  successToast?: { text: string };
  errorToast?: { text: string };
}

export interface BaseActionModal {
  title?: { text: string };
  actions?: {
    submit?: { text: string };
    cancel?: { text: string };
  };
  feedback?: Feedback;
}

export interface ActionModalField {
  id: string;
  label?: string;
}

export interface DeleteActionModal extends BaseActionModal {
  description?: { text: string };
}

export interface BulkDeleteActionModal extends DeleteActionModal {}

export type BulkDeleteAction =
  | {
      mode: 'modal';
      modal: BulkDeleteActionModal;
      custom?: never;
    }
  | {
      mode: 'custom';
      custom: CustomAction;
      modal?: never;
    };

export type DeleteAction = {
  mode: 'modal';
  modal: DeleteActionModal;
};

export interface BaseActionPage {
  id: string;
}

export type CustomAction = {};

export interface ActionModalWithFields extends BaseActionModal {
  fields: ActionModalField[];
}

export interface UpdateActionModal extends ActionModalWithFields {}

export interface CreateActionModal extends ActionModalWithFields {}

export interface UpdateActionPage extends BaseActionPage {}

export interface CreateActionPage extends BaseActionPage {}

export type UpdateAction = {
  mode: 'page';
  page: UpdateActionPage;
};

export type CreateAction = {
  mode: 'page';
  page: CreateActionPage;
};

export type CreateActionConfig = BaseActionItemConfig & {
  type: 'create';
  create: CreateAction;
};

export type UpdateActionConfig = BaseActionItemConfig & {
  type: 'update';
  update: UpdateAction;
};

export type DeleteActionConfig = BaseActionItemConfig & {
  type: 'delete';
  delete: DeleteAction;
};

export type BulkDeleteActionConfig = BaseActionItemConfig & {
  type: 'bulkDelete';
  bulkDelete: BulkDeleteAction;
};

export type CustomActionConfig = BaseActionItemConfig & {
  type: 'custom';
  custom: CustomAction;
};
